
.pending-po-container {
  max-width: 1200px;
  margin: 20px auto;
  font-family: Arial, sans-serif;
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

h2 {
  text-align: center;
  background-color: #f1f3f4;
  margin: 0;
  padding: 15px;
  border-bottom: 1px solid #ddd;
}

.message {
  color: red;
  text-align: center;
  padding: 10px;
}

/* NEW: Flex container for list+details */
.details-list-control {
  display: flex;
  width: 100%;
  height: 600px;
}

/* NEW: list-control to stack controls and list */
.list-control {
  width: 35%;
  display: flex;
  flex-direction: column;
  background-color: #f8f9fa;
  border-right: 1px solid #ddd;
}

/* Control buttons at the top */
.po-controls {
  padding: 10px;
  background-color: #f8f9fa;
  border-bottom: 1px solid #ddd;
  display: flex;
  gap: 10px;
  align-items: center;
}

.po-controls button {
  padding: 8px 12px;
  background-color: #c6d1df;
  color: black;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.po-controls button:hover {
  background-color: #79a8e5;
}

/* PO List */
.po-list {
  flex-grow: 1;
  overflow-y: auto;
}

