.po-item.active {
  background-color: #d2e3fc;
  font-weight: bold;
}

/* PO Details */
.po-details {
  width: 65%;
  padding: 20px;
  overflow-y: auto;
}

.item-details h3 {
  margin-top: 0;
  color: #202124;
}

.item-details p {
  margin: 8px 0;
  font-size: 15px;
  line-height: 1.5;
}

.loading, .empty-state {
  text-align: center;
  padding: 20px;
  color: #555;
}

/* Remove redundant classes */
.po-section {
  width: 100%;
}

.po-item {
  padding: 12px 15px;
  border-bottom: 1px solid #eee;
  cursor: pointer;
  transition: background-color 0.2s;
  display: flex;
  align-items: center;
  gap: 10px;
}

.po-item:hover {
  background-color: #e8f0fe;
}

.po-item input[type="checkbox"] {
  width: 18px;
  height: 18px;
  cursor: pointer;
}

.po-item-text {
  flex: 1;
}


.tab-buttons {
  display: flex;
  margin-bottom: 1rem;
}
.tab-buttons button {
  margin-right: 1rem;
  padding: 0.5rem 1rem;
}
.tab-buttons button.active {
  background-color: #007bff;
  color: white;
}
