
/* Container for the entire form */
.create-pr-wrapper {
  

  
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.top-right-home {
  display: flex;
  position: relative;
  width: 100%;
  padding: 0 1rem; /* optional padding */
  box-sizing: border-box;
  margin-bottom: 0;
   gap: 5rem;
}

.top-right-home h2 {
  text-align: center;
  margin: 0 auto 20px auto;
  color: #000000;
  background-color: #ffffff;
  font-size: 1.5rem;
  width: fit-content;
}

.top-right-home button {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  background-color: #000000;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
     gap: 2px;
}

.top-right-home button .icon {
  display: inline-block;
}

.top-right-home button .text {
  flex-grow: 1;
  text-align: center;
}

.top-right-home button:hover {
  background-color: #014b17;
}


/* Form layout */
.pr-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  background-color: #ffffff;

  
}

/* Modern card for each item */
.pr-item-card {
  background: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
}

/* Header for each item card */
.pr-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.pr-card-header h4 {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
}

/* Delete button */
.delete-btn {
  background: none;
  border: none;
  color: #e63946;
  font-size: 18px;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.delete-btn:hover {
  transform: scale(1.2);
}

/* Grid layout for item inputs */
.pr-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

/* Common input styling */
.form-group {
  display: flex;
  flex-direction: column;
}

label {
  font-weight: 500;
  margin-bottom: 6px;
  color: #444;
}

input,
textarea,
select {
  padding: 10px;
  font-size: 15px;
  border: 1px solid #ccc;
  border-radius: 6px;
  transition: border-color 0.3s ease;
  background-color: #fff;
}

input:focus,
textarea:focus,
select:focus {
  border-color: #1a73e8;
  outline: none;
}

textarea {
  resize: none;

}

input[readonly] {
  background-color: #f5f5f5;
  cursor: not-allowed;
}

/* For full-width fields like description */
.full-width {
  grid-column: 1 / -1;
}

/* Form action buttons */
.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 15px;
  margin-bottom: 20px;
}

.form-actions button {
  padding: 10px 16px;
  background-color: #000000;
  color: #fff;
  border: none;
  border-radius: 6px;
  font-weight: 600;
  font-size: 15px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.form-actions button:hover {
  background-color: #3e3e3e;
}

/* Status message */
.status-message {
  text-align: center;
  font-weight: 600;
  font-size: 16px;
  margin-top: 20px;
}

.status-message::before {
  content: 'ℹ️ ';
}

/* Dialog styles */
.dialog-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.dialog-box {
  background-color: white;
  color: #000000;
  padding: 2rem;
  border-radius: 10px;
  max-width: 500px;
  width: 100%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
  text-align: left;
}

.dialog-box h3 {
  margin-top: 0;
}

.dialog-box p {
  margin: 10px 0;
}

.dialog-box button {
  margin-top: 10px;
}

.dialog-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 1.5rem;
}

.dialog-actions button {
  padding: 8px 14px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  font-weight: 500;
  transition: background-color 0.3s ease;
}

.dialog-actions button:first-child {
  background-color: #000000;
  color: #fff;
}

.dialog-actions button:first-child:hover {
  background-color: #3e3e3e;
}

.dialog-actions button:last-child {
  background-color: #000000;
  color: #fff;
}

.dialog-actions button:last-child:hover {
  background-color: #3e3e3e;
}

/* Blur background when modal open */
.blurred {
  filter: blur(2px);
  pointer-events: none;
  user-select: none;
}

.dialog-box,
.dialog-box * {
  pointer-events: auto;
}


.pr-item-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 1rem;
}

.pr-item-table th,
.pr-item-table td {
  border: 1px solid #ccc;
  padding: 8px;
}

.pr-item-table input,
.pr-item-table textarea {
  width: 100%;
  box-sizing: border-box;
}

/* Container for each card */
.pr-item-card {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 1rem;
  margin-bottom: 1rem;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
  width: 100%;
  box-sizing: border-box;
}

/* Header of the card */
.pr-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.pr-card-header h4 {
  font-size: 1rem;
  margin: 0;
}

.pr-card-header .delete-btn {
  background-color: none;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.85rem;
}

/* Form layout */
.pr-grid {
  display: grid;
  grid-template-columns: 1fr; /* single column default */
  gap: 1rem;
}

/* Two columns on screens wider than 480px */
@media (min-width: 480px) {
  .pr-grid {
    grid-template-columns: 1fr 1fr;
  }

  .form-group.full-width {
    grid-column: span 2;
  }
}

/* Form group styles */
.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-size: 0.85rem;
  font-weight: 500;
  margin-bottom: 4px;
  color: #333;
}

.form-group input,
.form-group textarea {
  padding: 8px 10px;
  font-size: 0.85rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
}

.form-group textarea {
 resize: none;

}

.form-group input:focus,
.form-group textarea:focus {
  border-color: #4096ff;
  outline: none;
}

@media (max-width: 480px) {
  .top-right-home button {
    max-width: 90px;
    font-size: 0.75rem;
    padding: 4px 8px;
  }
}

@media (max-width: 600px) {
  .form-actions{
    justify-content: center;
  }
}
