.user-management-wrapper {
  max-width: 600px;
  margin: 40px auto;
  padding: 24px;
  background-color: #ffffff;
  border-radius: 12px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
  font-family: 'Segoe UI', sans-serif;
}

.user-management-wrapper h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

.user-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.user-form input[type="text"],
.user-form input[type="email"],
.user-form input[type="password"] {
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 16px;
}

.privileges-checkboxes {
  display: flex;
  flex-direction: column;
  gap: 6px;
  padding-top: 10px;
}

.privileges-checkboxes label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
}

.user-form button {
  padding: 12px;
  font-size: 16px;
  background-color: #0077cc;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.user-form button:hover {
  background-color: #005fa3;
}

.status-message {
  margin-top: 16px;
  text-align: center;
  font-weight: bold;
  color: green;
}

.status-message:empty {
  display: none;
}

.status-message.error {
  color: red;
}
.top-right-home {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
}

.top-right-home button {
  padding: 6px 14px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
}

.top-right-home button:hover {
  background-color: #0056b3;
}
