

/* Header */
.preview-wrapper h2 {
  text-align: center;
  color: #000000;
  background: transparent;
}

.requestor-info{
text-align: center;
}


/* === MOBILE CARD VIEW === */
.mobile-cards {
  display: block;
}

.preview-item {
  background: #ffffff;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: 15px;
}

.preview-item h4 {
  margin-bottom: 10px;
  color: #007bff;
}

/* === DESKTOP TABLE VIEW === */
.desktop-table {
  display: none;
}

@media (min-width: 768px) {

  .requestor-info{
text-align:left;
}
  .desktop-table {
    display: block;
    margin-top: 1rem;
    overflow-x: auto;
  }

  .mobile-cards {
    display: none;
  }

  .pr-table {
    width: 100%;
    border-collapse: collapse;
    font-size: 0.95rem;
  }

  .pr-table th,
  .pr-table td {
    padding: 10px;
    border: 1px solid #ccc;
    text-align: left;
    vertical-align: top;
  }

  .pr-table th {
    background-color: #f5f5f5;
    color: #000;
  }

  .pr-table td {
    background-color: #fff;
  }
}

/* === BUTTONS === */
.preview-actions {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin: 20px 0;
}

.preview-actions button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  color: white;
  background-color: #000000;
  transition: background-color 0.3s ease;
}

.preview-actions button:hover:enabled {
  background-color: #3e3e3e;
}

.preview-actions button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

/* === SUCCESS MESSAGE === */
.success-message {
  font-size: 18px;
  color: green;
  font-weight: bold;
  text-align: center;
}

/* === LOADER === */
.loader {
  border: 5px solid #f3f3f3;
  border-top: 5px solid #000000;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin: auto;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
