

body, html {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: transparent;
  height: 100%;
}

.login-container {
  display: flex;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}

.login-image {
  flex: 1;
  background-size: auto 300px;
  background-repeat: no-repeat;
  background-position: center;
   background-color: #d4d4d4;
}

/* Form section */
.login-form-container {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #d4d4d4;
  padding: 2rem;
}

/* .login-form {
  width: 100%;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
} */


.login-form {
  width: 100%;
  height: 500px;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  padding: 2rem;
  border: 1px solid #e0e0e0;
  border-radius: 16px;
  background-color: #ffffff;
  
    box-shadow:
    0 4px 20px rgba(0, 0, 0, 0.08),
    0 8px 40px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease-in-out;
}

.login-form h2 {
  margin-bottom: 1rem;
  text-align: center;
  color: #333;
  background-color: #ffffff;
}

.login-form input {
  padding: 0.8rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 1rem;
}

.login-form button {
  padding: 0.8rem;
  background-color: #222222;
  border: none;
  color: white;
  font-weight: bold;
  border-radius: 7px;
  cursor: pointer;
  font-size: 1rem;
}

.login-form button:disabled {
  background-color: #020202;
  cursor: not-allowed;
}

.error {
  color: red;
  font-size: 0.9rem;
  text-align: center;
}

.loading-overlay {
  position: absolute;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background: rgba(255, 255, 255, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #000000;
  border-top: 4px solid transparent;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
.divider {
  height: 1px;
  width: 100%;
  background-color: #ccc;
  margin: 2px 0;
  display: block;
  flex-shrink: 0;
}


.social-buttons {
  display: flex;
 
  justify-content: center;
  margin-left: 40%;
  margin-right: 40%;
}

.social-button {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.7rem 1rem;
  border-radius: 8px;
  font-weight: 600;
  text-decoration: none;
  color: white;
  transition: background-color 0.3s ease;
  font-size: 0.95rem;
}

.social-button img {
  width: 40px;
  height: 40px;
}

.error-container {
  min-height: 1.2rem; /* or however tall your error line is */
}

.error {
  color: red;
  font-size: 0.9rem;
  margin: 0;
}

.error-placeholder {
  visibility: hidden;
  margin: 0;
}



/* Responsive Design */
@media (max-width: 768px) {
  .login-container {
    flex-direction: column;
  }

  .login-image {
    background-size: auto 200px;
  }

  .login-form-container {
    flex: none;
    box-shadow: none;
    padding: 0;
 
  }

  .login-form {
    max-width: 100%;
    border-top-left-radius: 52px;  /* or any value you want */
  border-top-right-radius: 52px;
  border-bottom-left-radius: 0;  /* or any value you want */
  border-bottom-right-radius: 0;
  }
}
