.approved-vendors-container {
  max-width: 1200px;
  margin: 20px auto;
  font-family: Arial, sans-serif;
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

h2 {
  text-align: center;
  background-color: #f1f3f4;
  margin: 0;
  padding: 15px;
  border-bottom: 1px solid #ddd;
}

.message {
  color: red;
  text-align: center;
  padding: 10px;
}

.details-list-control {
  display: flex;
  width: 100%;
  height: 63vh;
}

.list-control {
  width: 35%;
  display: flex;
  flex-direction: column;
  background-color: #f8f9fa;
  border-right: 1px solid #ddd;
}

.vendor-list {
  flex-grow: 1;
  overflow-y: auto;
}

.vendor-item {
  padding: 12px 15px;
  border-bottom: 1px solid #eee;
  cursor: pointer;
  transition: background-color 0.2s;
}

.vendor-item:hover {
  background-color: #e8f0fe;
}

.vendor-item.active {
  background-color: #d2e3fc;
  font-weight: bold;
}

.vendor-details-section {
  width: 65%;
  padding: 20px;
  overflow-y: auto;
}

.vendor-details h3 {
  margin-top: 0;
  color: #202124;
}

.vendor-details p {
  margin: 8px 0;
  font-size: 15px;
  line-height: 1.5;
}

.vendor-details ul {
  padding-left: 20px;
}

.vendor-details li {
  margin: 5px 0;
}

/* STYLING FOR APPROVEPOPERVENDOR */

.view-pdf {
   display: flex;
   justify-content: flex-end;
}
