
.dialog-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.dialog {
  background: white;
  border-radius: 8px;
  width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  padding: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
}

.dialog h2 {
  margin-top: 0;
  font-size: 20px;
  margin-bottom: 16px;
}

.tabs {
  display: flex;
  margin-bottom: 16px;
}

.tabs button {
  flex: 1;
  padding: 10px;
  cursor: pointer;
  background-color: #f0f0f0;
  border: none;
  font-weight: bold;
  transition: background-color 0.3s;
}

.tabs button:hover {
  background-color: #e0e0e0;
}

.tabs .active {
  background-color: #007bff;
  color: white;
}

.template-tab {
  display: flex;
  gap: 20px;
}

.template-list {
  flex: 1;
  max-height: 250px;
  overflow-y: auto;
  padding-right: 8px;
  border-right: 1px solid #ddd;
}

.template-item {
  margin-bottom: 10px;
}

.template-item label {
  cursor: pointer;
  font-size: 14px;
}

.template-content {
  flex: 2;
  padding-left: 20px;
  border-left: 1px solid #ddd;
}

.template-content h4 {
  margin-top: 0;
}

.template-content ul {
  list-style: disc;
  margin-left: 20px;
  margin-bottom: 12px;
}

.manual-tab {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.manual-charge-row {
  display: flex;
  gap: 10px;
  align-items: center;
}

.manual-charge-row input[type="text"],
.manual-charge-row input[type="number"] {
  flex: 1;
  padding: 6px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.manual-charge-row button {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 6px 10px;
  border-radius: 4px;
  cursor: pointer;
}

.manual-charge-row button:hover {
  background-color: #b02a37;
}

.manual-tab > button {
  align-self: flex-start;
  background-color: #28a745;
  color: white;
  border: none;
  padding: 6px 12px;
  font-size: 14px;
  border-radius: 4px;
  cursor: pointer;
}

.manual-tab > button:hover {
  background-color: #218838;
}

.dialog-actions {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}

.dialog-actions button {
  background-color: #007bff;
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
}

.dialog-actions button:hover {
  background-color: #0056b3;
}


.manual-totalpo-field {
  margin-top: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.manual-totalpo-field input {
  width: 150px;
  padding: 0.4rem;
}
