.vendor-form {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
}

.vendor-box {
  border: 2px solid #ccc;
  padding: 20px;
  margin-bottom: 30px;
  border-radius: 8px;
  background-color: #fafafa;
}

.vendor-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.form-title {
  font-size: 20px;
  margin: 0;
}

.delete-vendor-btn {
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
}

.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 16px;
  margin-top: 16px;
}

.form-field {
  display: flex;
  flex-direction: column;
}

.form-label {
  margin-bottom: 5px;
  font-weight: 500;
}

.form-input {
  padding: 8px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.section {
  border: 1px solid #ddd;
  background: #fff;
  border-radius: 6px;
  padding: 15px;
  margin-top: 20px;
  display: block;
}

.section-title {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 12px;
}

.section-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  gap: 12px;
  align-items: end;
  margin-bottom: 12px;
}

.section-field {
  display: flex;
  flex-direction: column;
}

.add-button {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 8px;
}

.add-button:hover {
  background-color: #45a049;
}

.delete-button {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 6px 10px;
  border-radius: 4px;
  cursor: pointer;
  align-self: center;
}



.form-actions {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}

.submit-button {
  background-color: #2196f3;
  color: white;
  border: none;
  padding: 10px 16px;
  border-radius: 4px;
  cursor: pointer;
}

.submit-button:hover {
  background-color: #1976d2;
}

.top-right-home {
  display: flex;
  justify-content: flex-end;

}

.blurred {
  filter: blur(3px);
}









.item-box {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 16px;
  background-color: #fafafa;
}

.item-title {
  font-weight: bold;
  font-size: 16px;
  margin-bottom: 12px;
}

.section-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 12px;
}

.section-field {
  display: flex;
  flex-direction: column;
}

.form-label {
  margin-bottom: 4px;
  font-weight: 500;
}

.form-input {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.add-button, .delete-button {
  margin-top: 8px;
}

.delete-button {
  background: none;
  border:black;
  font-size: 18px;
  cursor: pointer;
  color: #f44336;
  align-self: center;
}



@media (max-width: 600px) {
  .form-actions{
    justify-content: center;
  }
}
