.po-details {
  border: 1px solid #ccc;
  padding: 20px;
  border-radius: 8px;
  background: #fefefe;
  /* width: fit-content; */

}

.po-section {
  margin-bottom: 15px;
}

.po-table {
  width: 100%;
  border-collapse: collapse;
}

.po-table th, .po-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.po-table th {
  background-color: #f1f3f4;
}

ul {
  margin: 0;
  padding-left: 16px;
}

.terms-row td {
  background-color: #f9f9f9;
  padding: 10px;
  font-style: italic;
  border-top: none;
}

.po-table td ul,
.po-table td ul li {
  margin: 0;
  padding: 0;
  list-style: none;
}
