.po-container {
  width: 100%;
  margin: 0 auto;
  font-family: Arial, sans-serif;
   position: relative;
}

.po-tile {
  border: 1px solid #ccc;
  padding: 16px;
  margin-bottom: 12px;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.po-tile-header {
  display: flex;
 
  align-items: center;
  flex-direction: row;
  /* width: 100%; */
}

.po-tile-header-view {
    display: flex;

   justify-content: space-between;
   /* width: 95%; */
   flex-grow: 1;
   padding-right: 20px;
  /* justify-content: stretch; */
}

.toggle-btn {
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
   margin-left: auto;
}

.po-tile-content {
  margin-top: 12px;
}

.form-group {

    display:flex;
    flex-direction: column;
     justify-content:space-between;

    
  padding-left: 1%;
     padding-right: 1%;
     
  border-radius: 8px;
  padding-top: 10px;

 

     
}

.form-group-detail-table {
  display: flex;
  flex-direction: column;
   background-color: #ccc;
   border-radius: 4px;
   margin: 10px;
}

.form-input {
  width: 50%;
  padding: 8px;
  margin-top: 4px;
  border: 1px solid #ccc;
  border-radius: 4px;

   display: flex;
}

.quantity-unit-vendor{
    display: inline-block;
    padding-right: 60px;
}

.add-vendor-btn {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
}

.add-vendor-btn:hover {
  background-color: #0056b3;
}

.vendor-entry {
  border: 1px solid #ddd;
  padding: 10px;
  margin: 10px 0;
  border-radius: 4px;
  background-color: #e8f0fe;
}

.vendor-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 6px;
}

.delete-vendor-btn {
  background-color: #000000;
  color: white;
  border: 1.5px solid #ccc;
  padding: 4px 8px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
}

.delete-vendor-btn:hover {
  color: #a71d2a;
}

