.vendor-option {
  margin: 8px 0;
}

.close-dialog-btn {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 12px;
}

.close-dialog-btn:hover {
  background-color: #1e7e34;
}

.submit-btn {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 10px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  display: block;
  margin: 20px auto;
}

.submit-btn:hover {
  background-color: #218838;
}

.cancel-btn {
  background-color: #6c757d;
  margin-left: 10px;
}

.cancel-btn:hover {
  background-color: #5a6268;
}

.vendor-dialog {
    position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60%;             /* Use percentage for responsive width */
  max-width: 1000px;        /* Optional: prevent excessive growth */
  height: auto;           /* Allow height to adjust based on content */
  max-height: 70%;        /* Prevent dialog from exceeding viewport height */
  background: white;
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 20px;
  z-index: 1000;
  box-shadow: 0px 4px 8px rgba(0,0,0,0.2);
  overflow-y: auto;       /* Add scroll if content overflows */
}







