/* Blur effect */
.blurred {
    filter: blur(5px);
    pointer-events: none;  /* disable interactions */
    user-select: none;
}

.vendor-dialog .close-dialog-btn.top-right {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
  cursor: pointer;
}

.vendor-dialog .close-dialog-btn.top-right:hover {
  background-color: #45a049;
}
.quantity-unit {
    display: flex;
    flex-direction: row;
     gap: 50px; 
    
}

.vendor-details {
  margin-top: 8px;
  margin-bottom: 8px;
   border: 1px solid #ccc;
    padding: 8px;
    background-color: #f3f3f3; 
    border-radius: 10px;
}

