.charges-container {
  margin-top: 20px;
  
}

.charges-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  
}
.charge-header {
  display: flex;
  justify-content: center;
  
}


.icon-column {
  width: 40px;
}

.charge-input {
  width: 100%;
  resize: none;
  box-sizing: border-box;
  overflow: hidden;
  min-height: 0 !important; 
}

.delete-charge-btn {
  cursor: pointer;
  background: transparent;
  border: none;
  font-size: 16px;
}


.charges-table {
   width: 95%;
  table-layout: auto;
  border-collapse: collapse;
  border: 1px solid #000000;
}

.charges-table th,
.charges-table td {
  padding: 8px;
  border: 1px solid #000000;
  vertical-align: top;
}

.charge-table {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.table-heading-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.table-heading-content h4 {
  margin: 0;
}

.edit-btn {
  padding: 6px 12px;
  cursor: pointer;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
}

.charges-table .icon-column {
  width: auto;
  text-align: center;
}