.preview-container {
  max-width: 800px;
  margin: 40px auto;
  padding: 24px;
  border-radius: 10px;
  background: #fff;
  box-shadow: 0 0 12px rgba(0,0,0,0.1);
  font-family: "Segoe UI", sans-serif;
}

.preview-container h2 {
  text-align: center;
  margin-bottom: 24px;
  color: #2c3e50;
}

.section {
  margin-bottom: 30px;
  display: inline;
}

.section h3 {
  margin-bottom: 12px;
  color: #34495e;
  border-bottom: 1px solid #ccc;
  padding-bottom: 6px;
}

.preview-box {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 12px 16px;
  margin-bottom: 12px;
  background-color: #fafafa;
}

.preview-box p {
  margin: 4px 0;
}

.button-group {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.edit-button, .submit-button {
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  font-weight: bold;
  font-size: 16px;
  cursor: pointer;
}

.edit-button {
  background-color: #f1c40f;
  color: #2c3e50;
}

.submit-button {
  background-color: #2ecc71;
  color: white;
}

.edit-button:hover {
  background-color: #f39c12;
}

.submit-button:hover {
  background-color: #27ae60;
}

.vendor-preview {
  border: 2px solid #ccc;
  padding: 20px;
  margin-bottom: 30px;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.vendor-heading {
  margin-bottom: 15px;
  color: #2c3e50;
  font-weight: bold;
  font-size: 20px;
  border-bottom: 1px solid #ddd;
  padding-bottom: 5px;
}

.preview-actions button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.success-message {
  font-size: 18px;
  color: green;
  font-weight: bold;
}

/* Loader Animation */
.loader {
  border: 5px solid #f3f3f3;
  border-top: 5px solid #000000;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
